@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap);
/* base styles */
body {
  font-family: Poppins, sans-serif;
  margin: 0;
  font-size: 1.1em;
}
ul, li, p, h1, h2, h3, h4 {
  margin: 0;
  padding: 0;
}
ul {
  list-style-type: none;
}
.btn {
  background: none;
  border: 2px solid #1f9751;
  padding: 6px 12px;
  border-radius: 4px;
  color: #1f9751;
  font-weight: bold;
  cursor: pointer;
  font-size: 1em;
}
.btn:hover {
  background: #1f9751;
  color: #fff;
}
.Navbar_navbar__31-FB {
    width: 100%;
    background: #effaf0;
    padding: 20px 10px;
    box-sizing: border-box;
}

.Navbar_navbar__31-FB ul {
    display: flex;
    margin: 0 auto;
    max-width: 960px;
    align-items: center;
}

.Navbar_title__2CGsW {
    margin-right: auto;
    font-weight: bold;
    letter-spacing: 1px;
    font-size: 1.2em;
}

.Navbar_navbar__31-FB button, .Navbar_navbar__31-FB a {
    margin-left: 16px;
}

.Navbar_navbar__31-FB a {
    color: #333;
    text-decoration: none;
}
.Home_container__RZ-hk {
display: grid;
grid-template-columns: 2fr 1fr;
max-width: 960px;
margin: 60px auto;
}

.Home_content__1rhYJ {
padding-right: 30px;
}

.Home_sidebar__2mPab {
padding-left: 30px;
}

.Home_sidebar__2mPab form {
padding: 20px;
background: #1f9751;
border-radius: 10px;
}

.Home_sidebar__2mPab input, .Home_sidebar__2mPab select {
display: block;
width: 100%;
padding: 10px;
margin-top: 8px;
box-sizing: border-box;
border: 0;
border-radius: 4px;
color: #555;
font-size: 1em;
}

.Home_sidebar__2mPab label {
margin: 0 auto 20px;
display: block;
color: #fff;
}

.Home_sidebar__2mPab button {
color: #fff;
border: 2px solid #fff;
padding: 6px 12px;
background-color: transparent;
font-size: 1em;
border-radius: 4px;
cursor: pointer;
display: block;
width: 100%;
}

.Home_sidebar__2mPab h3 {
color: #1f9751;
margin-bottom: 20px;
}

.Home_sidebar__2mPab aside {
margin-top: 40px;
color: #555;
}

.Home_sidebar__2mPab aside li {
margin: 10px;
}

/* transactions */
.Home_transactions__FabFe li {
